<!--直播设置-->
<template>
  <div class="live-setting">
    <div class="title">
      直播设置
      <div class="btn-lists">
        <btn-list @click.native="handleAddLive" :model="{icon:'icon-add',name:'添加'}"></btn-list>
        <btn-list @click.native="handleDelete" :model="{icon:'icon-del',name:'删除'}"></btn-list>
      </div>
    </div>
    <div class="container">
      <el-table @select="handleSelect" @select-all="handleSelect" :data="tableData" style="width: 100%">
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column align="center" width="100" prop="i" label="序号"></el-table-column>
        <el-table-column align="center" width="100" label="直播名称">
          <template slot-scope="scope">
            <span class="text-overflow" @click="$router.push(`/liveCenter/Layout/liveSetting/detail/${scope.row.liveRoomId}`)" style="color:#4A80F6;cursor:pointer">{{scope.row.liveRoomName}}</span>
          </template>
        </el-table-column>
        <el-table-column align="center" label="直播观看地址">
          <template slot-scope="scope">
            <div class="text-overflow">{{scope.row.lookUrl}}</div>
          </template>
        </el-table-column>
        <el-table-column align="center" label="推流地址">
          <template slot-scope="scope">
            <div class="text-overflow">{{scope.row.rtmpUrl}}</div>
          </template>
        </el-table-column>
        <el-table-column align="center" width="100" label="创建方式">
         <template slot-scope="scope">
            <div>{{createType[scope.row.createType]}}</div>
          </template>
        </el-table-column>
        <el-table-column align="center" label="状态">
          <template slot-scope="scope">
            <div>{{liveStatus[scope.row.liveStatus]}}</div>
          </template>
        </el-table-column>
        <el-table-column align="center" width="150" label="是否开启">
          <template slot-scope="scope">
            <el-switch
              :key="scope.row.liveRoomId"
              @change="handleChangeStatus(scope.row)" 
              v-model="scope.row.isOpen"
              :active-value="1"
              :inactive-value="0" 
              active-color="#4A80F6" 
              inactive-color="#ccc">
            </el-switch>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="60">
          <template slot-scope="scope">
            <div class="control" @mouseenter="scope.row.show_control = true" @mouseleave="scope.row.show_control = false">
              <div v-if="scope.row.show_control" class="control-item">
                <div @click="$router.push(`/liveCenter/Layout/liveSetting/detail/${scope.row.liveRoomId}`)">查看</div>
                <div @click="$router.push(`/liveCenter/Layout/liveSetting/update/${scope.row.liveRoomId}`)">编辑</div>
                <div @click="handleDeleteLiveRoom(scope.row.liveRoomId)">删除</div>
              </div>
              <i v-else class="iconfont icon-more"></i>
            </div>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <el-pagination
        style="margin-top:20px;text-align:right"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="pagination.pageNo"
        :page-sizes="[10, 20, 30, 40]"
        :page-size="pagination.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalDatas">
      </el-pagination>
    </div>
  </div>
</template>

<script>
export default {
  components: {
    btnList: () => import("@/components/btn-list")
  },
  data() {
    return {
      totalDatas: 0,
      pagination:{
        pageNo:1,
        pageSize:10
      },
      liveStatus:{9010:"未直播",9011:"直播中",9012:"直播结束"},
      createType:{9070:"预约",9071:"手动"},
      tableData: [],
      selected:''
    };
  },
  created(){
    this.getTableData()
  },
  methods:{
    getTableData(){
      this.$api.selectPageInfo(this.pagination).then(res => {
        this.tableData = res.data.map((item,i)=>Object.assign(item,{show_control:false,i:i+1}));
        this.totalDatas = Number(res.totalDatas);
      })
    },
    /* 当前页数改变 */
    handleCurrentChange(current){
      this.pagination.pageNo = current;
      this.getTableData();
    },
    /* 当前页size改变 */
    handleSizeChange(pageSize){
      this.pagination= {
        pageNo:1, pageSize
      }
      this.getTableData();
    },
    // 状态的改变
    handleChangeStatus(row){
      const params = {
        isOPen:row.isOpen,
        liveRoomId:row.liveRoomId
      }
      this.$api.switchLiveromm(params).then(res => {
        this.$myLayer.successLayer("更新成功")
      })
    },
    /* 删除 */
    handleDeleteLiveRoom(liveRoomId){
      const params = {liveRoomIds:liveRoomId}
      this.$myLayer.confirmLayer("确认删除？",()=> {
        this.$api.deleteLiveroom(params).then(res => {
          this.pagination.pageNo = 1;
          this.getTableData()
        })
      })
    },
    /* 选中 */
    handleSelect(selection){
      this.selected = selection.map(item => item.liveRoomId).join(',');
    },
    /* icon delete */
    handleDelete(){
      if(!this.selected) return;
      this.handleDeleteLiveRoom(this.selected)
    },
    handleAddLive(){
      this.$router.push("/liveCenter/Layout/liveSetting/create")
    }
  }
};
</script>

<style lang="scss" scoped>
.live-setting {
  padding: 32px;
  min-height: 100%;
  // height: 100%;
}
.live-setting .title {
  position: relative;
  line-height: 1;
  .btn-lists {
    position: absolute;
    right: 0;
    top: 0;
  }
}
.container {
  margin-top: 23px;
  height: calc(100% - 37px);
  background: #fff;
  border-radius: 6px;
  padding: 24px;
}

.control {
   height: 49px;
   width: 60px;
   position: absolute;
   left: 0;
   top: 0;
   line-height: 49px;
   text-align: center;
  .control-item {
    position: absolute;
    height: 49px;
    width: 176px;
    right: 0;
    background: #d1e9ff;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 24px;
    & > div {
      cursor: pointer;
      color: #787D82;
    }
    & > div:hover {
      color: #4A80F6;
    }
  }
}
</style>